@import './palette.scss';
.grid-container {
    display: grid;
    grid-template-columns: auto auto;
    padding: 10px;
    width: 100%;
  }
.grid-item {
    margin: 20px;
    font-size: 30px;
    text-align: center;
    height: 400px;
    transition: box-shadow .4s, background-color .4s;
    color: white;
    position: relative;
}
.clickableBox{
    border-radius: 10px;
    cursor: pointer;
}
.projectPageBody{
    width: 100%;
    height: auto;
    top: 0;
}
.projectCard{
    border-radius: 10px;
      background-size: cover;
      background-repeat: no-repeat;
      background-color: transparent;
      height: 100%;
      width: 100%;
      
}

.clickableBox:hover {
    box-shadow: 4px 4px 10px $BlueShadow;
}
.cardTitle{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    //text-shadow: 0px 0px 4px black;
    background-color: rgba(3, 78, 99,.95);
    max-height: 100%;
    overflow: hidden;
    
    border-bottom-left-radius: 10px;
    
    border-bottom-right-radius: 10px;
}
.overlay{
    
    border-radius: 10px;
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    background: linear-gradient(rgba(0, 0, 0,1), rgba(0, 0, 0, .4));
    opacity: .6;
    transition: all .5s;
}
.overlay:hover{
    
    border-radius: 10px;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background: linear-gradient(rgba(0, 0, 0,1), rgba(0, 0, 0, .4));
    opacity: .1;
    transition: all .5s;
}
.cardDescription{
    //text-shadow: 0px 0px 2px black;
    border-top: 1px solid black;
    background-color: rgba(195, 241, 253,.2);
    bottom: 0;
    border-bottom-left-radius: 10px;
    
    border-bottom-right-radius: 10px;
}

@media (max-width: 1000px) {
    .grid-container {
        grid-template-columns: auto;
    }
}

@media (min-width: 1700px) {
    .grid-container {
        grid-template-columns: auto auto auto;
    }
}

.githubButton {
    border: 2px solid #fff;
    box-sizing: inherit;
    cursor: pointer;
    display: inline-block;
    font-size: 1em;
    padding: 5px 10px;
    margin: 1em;
    transition: all 0.5s;
    background: transparent;
    color: white;
    background: rgba(0, 0, 0,.7);
  }
.githubButton:hover{
    background: #0090b8;
    border: 2px solid #fff;
  }